import Header from './Header'
import Galleria from './Galleria'
import './Galleria.css'

const layoutStyle = {
  margin: 20,
  padding: 20,
  border: '1px solid #DDD'
}

const Layout = (props) => (
  <div style={layoutStyle}>
    <Galleria />
    <Header />
    <div className={galleria}>
      {props.children}
    </div>
  </div>
)

export default Layout



